import { UPDATE_TRACKING, isTagShop, TagShopEditor, isTagbox, isSocialWall, isSocialEmbed, sw, ts, tbox, http, Pwidget, Pdisplay, tagembed, test, AUTH_TOKEN } from '../actions/api';
import { dataSource } from '../actions/themeActions'
import store from '../store';
export let URLDATA = []

export const convertObjectToArray = obj => Object.values(obj);
export const convertObjectToArray_Tags = obj => Object.entries(obj).map(([tagName, postCount]) => ({ tagName, postCount }));
const getDevidedValue = (v1, v2) => v1 / v2;
const postWidthAdjustSpacing = (c, r, w, p) => { const s = w % p, a = s / r, u = c >= 1 ? p + a : p - a; return { spacing: s, width: u, adjustSpacing: a } };
const defaultColumn = (v) => v < 575.98 ? 1 : v < 767.98 ? 2 : v < 991.98 ? 3 : v < 1199.98 ? 4 : 5;


var currentTheme = null;
export const activeTheme = (e) => currentTheme = e

export const tagSocialWall = 'tagshop-socialwall'
export const tagClass = window.isTagShop ? 'tagshop-socialwall' : 'taggbox'

export const bestFitRow = (windowWidth, wall) => {

    const { ThemeStyle, CardStyle } = wall
    try {
        const { Posts } = store.getState().appData;
        const postLength = Posts.Posts.length;

        const minWidth = Math.floor(windowWidth / CardStyle.minimumWidth);
        return ThemeStyle.row === 1 && postLength >= minWidth
            ? ThemeStyle.column.desktop
            : postLength;
    } catch {
        return ThemeStyle.column.desktop;
    }
};


export const THEME_WIDTH_ADJUSTMENT_MODERN = (wnWidth, wall) => {
    const { ThemeInfo, ThemeStyle, CardStyle } = wall;
    const { column } = ThemeStyle
    const themeId = isSocialWall ? [70, 64] : [3, 4, 5, 20, 19, 62, 79, 83];
    let cardNumber = null;
    let adjustWidth = null;

    const calculateWidthAndColumns = (singlePostWidth) => {
        cardNumber = getDevidedValue(wnWidth, singlePostWidth);
        adjustWidth = postWidthAdjustSpacing(cardNumber, Math.trunc(cardNumber), wnWidth, singlePostWidth).width - 0.8;
        return { adjustWidth, cardNumber };
    };

    const getNumberOfColumns = () =>
        isTagShop && ThemeStyle.bestFit === 1 && column.desktop === 0
            ? bestFitRow(wnWidth, wall)
            : parseInt(detectMobile() && window.innerWidth < 768 ? column.mobile : column.desktop);


    if (themeId.includes(ThemeInfo.id)) {
        const numberOfColumns = getNumberOfColumns();
        const singlePostWidth = numberOfColumns > 0 ? (wnWidth / numberOfColumns) : CardStyle.minimumWidth;
        return calculateWidthAndColumns(singlePostWidth);
    }

    if ([50].includes(ThemeInfo.id)) {
        const numberOfColumns = column.desktop === 0 ? defaultColumn(wnWidth) : parseInt(column.desktop);
        return wnWidth > CardStyle.minimumWidth
            ? calculateWidthAndColumns(wnWidth / numberOfColumns - 10)
            : { adjustWidth: wnWidth, cardNumber: 1 };
    }

    if (ThemeInfo.id === 67) {
        const columnMap = { 8: 4, 12: 4, 18: 6, 32: 8 };
        const numberOfColumns = isSocialEmbed ? column.desktop : columnMap[column.desktop] || 4;
        return calculateWidthAndColumns(numberOfColumns > 0 ? wnWidth / numberOfColumns : CardStyle.minimumWidth);
    }

    if (wnWidth > CardStyle.minimumWidth) {
        return calculateWidthAndColumns(CardStyle.minimumWidth);
    }

    return { adjustWidth: wnWidth, cardNumber: 1 };
};

export const getWallID = () => {

    const urlIncludes = (keywords) => keywords.some((keyword) => window.location.href?.includes(keyword));
    const queryWallId = getQueryString(window.location.href, 'wall_id');

    if (queryWallId) return queryWallId;

    const wall_Id = window.wall_Id

    if (document.getElementsByClassName("wall-editor")?.length && wall_Id) return wall_Id;
    if (urlIncludes(["localhost"]) && wall_Id) return wall_Id;

    if (urlIncludes(["widget-lite"])) {
        const baseURL = `https://${Pwidget}-lite.${tbox}/`;
        const wallPath = window.location.href.split(baseURL)?.[1];
        return wallPath?.split("?")?.[0] || wall_Id;
    }

    if (urlIncludes([`${test}${tbox}/embed-${Pwidget}/lite`])) {
        const container = document.getElementsByClassName("taggbox");
        for (const element of container) {
            const { isLoad, renderId, widgetId } = element.dataset
            if (isLoad === "0") {
                document
                    .getElementById(`co_${renderId}`)
                    ?.setAttribute("data-is-load", "1");
                return widgetId;
            }
        }
    }

    const platformURLs = [
        `${Pdisplay}-${test}${sw}`,
        `${Pdisplay}.${sw}`,
        `${Pwidget}.${sw}`,
        `${Pwidget}-${test}${tagembed}`,
        `${Pwidget}s.${tagembed}`,
        `${Pwidget}.${tagembed}`,
        `${Pwidget}-${test}${ts}`,
        `${Pwidget}.${ts}`,
        `${test}${ts}/w/`,
        `${test}${ts}/s/`,
    ];

    if (urlIncludes(platformURLs)) {
        const splitURL = platformURLs.find((key) => wUrl(key)) || `${Pwidget}.${tbox}/`;
        const baseUrl = `${http}${splitURL.includes(`${test}${ts}`) ? splitURL : `${splitURL}/`}`;
        const wallPath = window.location.href.split(baseUrl)?.[1];
        return wallPath?.split("?")?.[0] || window.wall_Id;
    }

    const socialWallClass = document.getElementsByClassName(tagSocialWall);
    const tagClassValue = document.getElementsByClassName(tagClass);
    const container = socialWallClass?.length ? socialWallClass : tagClassValue;

    for (const element of container) {

        const { isLoad, wallId, widgetId, renderId } = element.dataset

        if (isLoad === "0") {
            document
                .getElementById(`co_${renderId}`)
                ?.setAttribute("data-is-load", "1");
            return socialWallClass?.length
                ? wallId
                : isTagShop
                    ? wallId
                    : widgetId;
        }
    }

    return null;
};

export const tagShopRender = () => {
    if (window.isTagShop || String(window.location.href).includes("tagshop.ai")) return true
    else return false
}

export const tagEmbedRender = () => {
    if (String(window.location.href).includes("widgets.tagembed")) return true
    else return false
}

export const ISLOCALHOST = (window.location.href && window.location.href.includes("localhost") && window.location.href.includes("widget") && window.location.href.includes("wall_id=")) ? true : false
export const mainClass = !ISLOCALHOST && (tagEmbedRender() || window.location.href.includes("widget.tagembed") || window.location.href.includes("widget-test.tagembed") || window.location.href.includes("widgets.tagembed")) ? 'tag_main__root' : !ISLOCALHOST && (tagShopRender()) ? 'taggShopRoot' : 'taggbox_main'

export const getRenderContainer = () => {
    const parsed = queryString(window.location.search);
    if (parsed.wall_id || parsed.addfeedload == "load") {
        if (parsed.addfeedload == "load" && (window.location.href.includes("widget.tagembed") || window.location.href.includes("widget-test.tagembed"))) return 'tag_main__root';
        else return 'taggbox_main'
    }
    // else if (document.getElementsByClassName('wall-editor')) return 'taggbox_main');
    else if (window.location.href && window.location.href.includes("localhost") && (window.wall_Id || parsed.wall_id)) {
        return 'taggbox_main'
    }

    else if (window.location.href && window.location.href.includes("reactWebVital")) return 'taggbox_main'
    else if (window.location.href && window.location.href.includes("reactStaticWebVital")) return 'taggbox_main'
    else if (window.location.href && window.location.href.includes("widget-lite")) return 'taggbox_main'
    else if (window.location.href && (window.location.href.includes("test.taggbox.com/embed-widget/lite") || window.location.href.includes("test.taggbox.com/embed-widget-optimized/lite"))) return extendsGetRenderContainer()
    else if (window.location.href && (window.location.href.includes("widget.taggbox") || window.location.href.includes("app.taggbox") || window.location.href.includes("test.taggbox") || window.location.href.includes("widget.tagembed") || window.location.href.includes("widget-test.tagembed") || window.location.href.includes("widgets.tagembed") || window.location.href.includes("widget-test.tagshop.ai") || window.location.href.includes("widget.tagshop.ai") || window.location.href.includes("test.tagshop.ai/w/") || window.location.href.includes("test.tagshop.ai/s/") || (document.getElementsByClassName('wall-editor') && window.wall_Id))) return mainClass
    else return extendsGetRenderContainer()
}
 

export const extendsGetRenderContainer = () => {  
    try {
        var socialwall = document.getElementsByClassName(tagSocialWall).length
        var tagembed = document.getElementsByClassName(tagClass).length
        if (socialwall && tagembed) {
            if (document.getElementsByClassName(tagSocialWall)) {
                const tagembedSocial = document.getElementsByClassName(tagSocialWall)[0]
                if (tagembedSocial && tagembedSocial.getAttribute("data-render-id") && tagembedSocial.getAttribute("data-is-load") == "1") {
                    socialwall = 0
                }
            }
        }

        var container = socialwall && socialwall > 0 ? document.getElementsByClassName(tagSocialWall) : document.getElementsByClassName(tagClass);

        for (var i = 0; i < container.length; i++) {
            if (container[i].getAttribute("data-is-load") == "0") {
                if (container[i].getAttribute('data-render-id')) rID = container[i].getAttribute('data-render-id')
                return container[i].getAttribute("data-render-id")
            }
        }
    }
    catch (ex) {  
        return document.getElementById('taggbox_main');
    }
}

export let rID;

