import React from "react";
import ReactDOM from "react-dom/client"; // React 19 uses 'react-dom/client'
import { Provider } from "react-redux";
import "./App.scss";
import store from "./store";
import Main from "./main";
import { extendsGetRenderContainer } from "./utils";

const  taggbox_main = document.getElementById('taggbox_main')

if(!taggbox_main)  extendsGetRenderContainer()
const getRoot = taggbox_main || document.querySelector('.tagshop-socialwall'); // Ensure it returns a DOM element
 
if (getRoot) { // Only create the root if an element exists
    const root = ReactDOM.createRoot(getRoot);
    root.render(
        <Provider store={store}>
            <Main />
        </Provider>
    );
} else {
    console.error("Root container not found. Ensure the target element exists in the DOM.");
}
