import axios from "axios";
import { MORE_LOADER} from "../reducers/reducersKeys";
import store from "../store";
import { rID, urlDiscardMail } from "../utils";
import { GET_POST,  isTagShop , UPDATE_TRACKING } from "./api";
import HttpClient from "./http-client"; 
import { dispatchMoreData, getAttributes, getHashTagParam } from "./commonAction";
export let dataSource = 'website';
export let dataRegion;

export const gtmAnalytics = (eventName, action, category, value) => {
    const { google_tacking_status } = store.getState().appData?.wall?.Wall || {};
    google_tacking_status === 1 &&
        window.gtag?.('event', action, {
            event_category: category,
            event_label: eventName,
            value,
        });
};
 
var call;

export const nextData = (
    wallID,
    timeStamp,
    postCount,
    networkId,
    after,
    heightEvent
) => async (dispatch) => {
    let updateUrl = `${GET_POST}${wallID}/${timeStamp}/${postCount}/${networkId || 0}/1/${after}`;

    if (isTagShop && getAttributes(rID)?.isStatus)  updateUrl += `/${getAttributes(rID).mUrl}`;

    const { wall, postData } = store.getState().appData;
    const { isFilterLoading } = store.getState().loaderData;

    dispatch({ type: MORE_LOADER, payload: true });

    const updateHeader = getHashTagParam(rID, wallID) || { headers: {} };
    const { headers } = updateHeader;

    const tagName = postData.appendData.tagName
    headers.tag = headers.tag || (tagName ? 1 : undefined);
    headers.keywords = headers.keywords || tagName || undefined;
    headers.action = 2;

    if (call) call.cancel();
    call = axios.CancelToken.source();
    updateHeader.cancelToken = call.token;

    if (!isFilterLoading) {
        themePostTracking({
            action: 1,
            wall: wallID,
            feed: "",
            post: "",
            owner: wall?.ThemeInfo.ownerId,
        });

        gtmAnalytics("Tagbox", "showmore_click", "showmore", wallID);

        try {
            const apiResponse = await new HttpClient().get(updateUrl, updateHeader);
            const { data } = apiResponse;
            dispatchMoreData(dispatch, data.data, networkId, {
                after: data.after,
                networkID: networkId,
                heightEvent,
                tagName,
            }, tagName, postCount, data.hasPost);

        } catch (error) {
            console.error(error);
        } finally {
            dispatch({ type: MORE_LOADER, payload: false });
        }
    }
} 


export const loadDelaydata = () => (dispatch) => {
    const { postData, delayLoad } = store.getState().appData;
    const { appendData } = postData;
    dispatchMoreData(dispatch, delayLoad, appendData.networkID, appendData, appendData.tagName);
};

export const themePostTracking = (data) => {
    if (urlDiscardMail()) {
        const updatedData = dataSource ? { ...data, model: dataSource } : data 
        new HttpClient().post(`${UPDATE_TRACKING}`, updatedData)
            .catch(() => {})
    }
}